import { Searcher, SignEditor } from '@fui/core';
import { shortcut } from '@engine/core';
import { ParameterSingleSelectPopup } from './singleselectpopup';
import { ParameterSingleSelectSearchPopup } from './singleselectsearchpopup';

@shortcut()
export class ParameterSingleSelectTrigger extends BI.Trigger {
    public static EVENT_SEARCHING = 'EVENT_SEARCHING';
    public static EVENT_STOP = 'EVENT_STOP';
    public static EVENT_START = 'EVENT_START';
    public static EVENT_CHANGE = 'EVENT_CHANGE';
    public static EVENT_FOCUS = 'EVENT_FOCUS';
    public static EVENT_BLUR = 'EVENT_BLUR';
    public static xtype = 'report.single_select_trigger';
    public static EVENT_CLICK = 'EVENT_CLICK';
    public static EVENT_KEY_DOWN = 'EVENT_KEY_DOWN';

    public props = {
        baseCls: 'bi-search-text-value-trigger bi-border bi-focus-shadow',
        height: 24,
        itemsCreator: BI.emptyFn as Function,
        valueGetter: () => undefined as {text: string; value: string} | undefined,
        unmatchedValueGetter: () => [] as {text: string; value: string}[],
        watermark: '',
        allowEdit: false,
        customData: true,
        itemHeightGetter: () => 24,
        value: '',
    }

    private searcher: Searcher;

    private editor: SignEditor;

    private popup: ParameterSingleSelectSearchPopup;

    public render() {
        const { height, itemsCreator, valueGetter, watermark, unmatchedValueGetter, customData, itemHeightGetter } = this.options;

        return {
            type: BI.HTapeLayout.xtype,
            items: [
                {
                    el: {
                        type: BI.Searcher.xtype,
                        ref: (_ref: Searcher) => {
                            this.searcher = _ref;
                        },
                        isAutoSearch: false,
                        el: {
                            type: BI.SignEditor.xtype,
                            ref: (_ref: SignEditor) => {
                                this.editor = _ref;
                            },
                            value: valueGetter()?.text || '',
                            allowBlank: true,
                            watermark,
                            height,
                            listeners: [{
                                eventName: BI.SignEditor.EVENT_FOCUS,
                                action: () => {
                                    this.editor.setValue('');
                                    this.fireEvent(ParameterSingleSelectTrigger.EVENT_FOCUS);
                                },
                            }, {
                                eventName: BI.SignEditor.EVENT_BLUR,
                                action: () => {
                                    this.fireEvent(ParameterSingleSelectTrigger.EVENT_BLUR);
                                },
                            }, {
                                eventName: BI.SignEditor.EVENT_CLICK,
                                action: () => {
                                    this.fireEvent(ParameterSingleSelectTrigger.EVENT_CLICK);
                                },
                            }, {
                                eventName: BI.SignEditor.EVENT_KEY_DOWN,
                                action: () => {
                                    this.fireEvent(ParameterSingleSelectTrigger.EVENT_KEY_DOWN);
                                },
                            }],
                        },
                        popup: {
                            type: ParameterSingleSelectSearchPopup.xtype,
                            customData,
                            cls: 'bi-card',
                            ref: (_ref: ParameterSingleSelectSearchPopup) => {
                                this.popup = _ref;
                            },
                            keywordGetter: () => this.editor.getValue(),
                            unmatchedValueGetter,
                            itemsCreator,
                            valueGetter,
                            itemHeightGetter,
                            listeners: [{
                                eventName: ParameterSingleSelectPopup.EVENT_CHANGE,
                                action: (text: string, value: string, isSelected: boolean) => {
                                    this.fireEvent(ParameterSingleSelectTrigger.EVENT_CHANGE, text, value, isSelected);
                                },
                            }],
                        } as Partial<ParameterSingleSelectSearchPopup['props']>,
                        onSearch: (_op: { keyword: string }, callback: Function) => {
                            callback();
                        },
                        listeners: [{
                            eventName: BI.Searcher.EVENT_CHANGE,
                            action: () => {
                                
                            },
                        }, {
                            eventName: BI.Searcher.EVENT_START,
                            action: () => {
                                this.fireEvent(ParameterSingleSelectTrigger.EVENT_START);
                            },
                        }, {
                            eventName: BI.Searcher.EVENT_SEARCHING,
                            action: () => {
                                this.fireEvent(ParameterSingleSelectTrigger.EVENT_SEARCHING);
                            },
                        }, {
                            eventName: BI.Searcher.EVENT_STOP,
                            action: () => {
                                this.fireEvent(ParameterSingleSelectTrigger.EVENT_STOP);
                            },
                        }],
                    },
                },
                {
                    el: {
                        type: BI.Layout.xtype,
                        width: 24,
                    },
                    width: 24,
                },
            ],
        };
    }

    public mounted() {
        !this.options.allowEdit && BI.createWidget({
            type: BI.AbsoluteLayout.xtype,
            element: this,
            items: [{
                el: {
                    type: BI.Text.xtype,
                    title: () => this.editor.getValue(),
                },
                left: 0,
                right: 24,
                top: 0,
                bottom: 0,
            }],
        });
        BI.EventListener.listen(this.element.context, 'keydown', (evt: KeyboardEvent) => {
            if (evt.keyCode === BI.KeyCode.ENTER) {
                const input = this.editor.getValue();
                const selectedItem = this.popup.getAllLeaves()
                    .find(item => item.text === input)
                    || { text: input, value: input };
                this.options.customData && this.fireEvent(ParameterSingleSelectTrigger.EVENT_CHANGE, selectedItem.text, selectedItem.value, true);
            }
        });
    }

    public setValue(val?: {text: string, value: string}) {
        this.editor.setValue(val?.text || '');
    }

    public stopEditing() {
        this.searcher.stopSearch();
    }

    public getSearcher() {
        return this.searcher;
    }

    public getValue() {
        return this.popup.getValue();
    }

    public setAdapter(adapter: any) {
        this.searcher.setAdapter(adapter);
    }

    public adjustPopupHeight(adapter: any) {
        this.setAdapter(adapter);
        this.searcher.populate();
        this.searcher.adjustHeight();
    }

    public isSearching() {
        return this.searcher.isSearching();
    }

    public focus() {
        this.editor.focus();
    }

    public blur() {
        this.editor.blur();
    }

    public setWaterMark(v: string) {
        this.editor.setWaterMark(v);
    }

    public selectNextItem() {
        this.popup.selectNextItem();
    }

    public selectPreItem() {
        this.popup.selectPreItem();
    }
}
